import introController from './Controller.js'
/* @ngInject */
export default function routes($stateProvider){

$stateProvider.state("time",{
  url:"/time",
  views:{
    "pages@":{
      template:require("./tpl.html"),
      controller: "TimeController",
      controllerAs:"Time"

    }
  }


})


}



